*,
*::before,
*::after {
  box-sizing: border-box;
}

.flex {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}

.--just-space {
  justify-content: space-between;
}

.--just-center {
  justify-content: center;
}

.--just-end {
  justify-content: flex-end;
}

.--align-str {
  align-items: stretch;
}

.--align-center {
  align-items: center;
}

.--align-end {
  align-items: flex-end;
}

.--dir-col {
  flex-direction: column;
}

html {
  font-size: 16px;
}

html,
body {
  min-height: 100%;
  position: relative;
  font-family: "Tenor Sans", sans-serif;
  font-weight: 400;
  font-style: normal;
}

body {
  font-size: 1rem;
  background-color: var(--surface-ground);
}
ul, ol, li, p {
  margin-left: 0;
  margin-right: 0;
  margin-top: 0;
  padding-bottom: 0;
  padding-left: 0;
  padding-right: 0;
  padding-top: 0;
  color: var(--text-color);
  font-weight: 300;
  text-rendering: optimizeLegibility;
  line-height: 1.4;
}

h1, h2, h3, h4, h5, h6 {
  margin-left: 0;
  margin-right: 0;
  margin-top: 0;
  padding-bottom: 0;
  padding-left: 0;
  padding-right: 0;
  padding-top: 0;
  margin-bottom: 1.5rem;
  color: var(--text-color);
  //font-family: 'Comfortaa', Arial, cursive;
  font-weight: 600;
  text-rendering: optimizeLegibility;
}

h1 {
  font-size: 2rem;
  line-height: 2.25rem;
}

h2 {
  font-size: 1.51572rem;
  line-height: 2.25rem;
}

h3 {
  font-size: 1.31951rem;
  line-height: 2.25rem;
}

h4 {
  font-size: 1rem;
  line-height: 1.5rem;
}

h5 {
  font-size: 0.87055rem;
  line-height: 1.5rem;
}

h6 {
  font-size: 0.81225rem;
  line-height: 1.5rem;
}

p {
  margin-bottom: 1rem;
  font-weight: 300;
  line-height: 1.4;
}

.page {
  &__title {
    font-size: 1.25rem;
    font-weight: 500;

    @media screen and (min-width: 992px) {
      font-size: 1.75rem;
    }
  }
}

.img {
  &__fluid {
    width: 100%;
    height: auto;
  }
}

.tag {
  color: var(--tag-color);
  font-size: 1rem;
  line-height: 1.4;
  font-weight: 400;
}

.divider {
  width: 100%;
  height: 2px;
  background-color: var(--black);
  margin: 2rem 0;
}

.button {
  border-radius: .5rem;
  background-color: var(--surface-ground);
  color: var(--primary-color-text);
  border: 1px solid var(--primary-color-text);
  font-size: 1rem;
  padding: .5rem;
  text-decoration: none;
  transition: background-color ease-in-out .4s;
  cursor: pointer;

  &:hover {
    background-color: var(--primary-color-text);
    color: var(--surface-ground);
  }

  &.button--large {
    font-size: 1.5rem;
    padding: 1rem;
  }

  &.button--medium {
    font-size: 1.25rem;
    padding: .875rem;
  }
}

input, textarea {
  width: 100%;
  border: 1px solid var(--surface-border);
  padding: 1rem;
  box-shadow: none;
  border-radius: .5rem;
  resize: none;

  &:focus {
    outline-color: var(--primary-color-text);
  }
}

.label {
  display: block;
  font-size: .875rem;
  font-weight: 500;
  color: var(--black);
  margin-bottom: .5rem;
}
