.container {
  padding: 2rem;
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 40rem;
  margin: 0 auto;
}

.block-image {
  width: 100%;
  max-width: 37.5rem;
  height: auto;
  object-fit: cover;
  object-position: top;
  margin: 0 auto;
}

.time {

  &-left {
    height: 100%;
    padding-right: 1rem;
  }

  &-right {
    border-left: 1px solid var(--gray-900);
    padding-left: 1rem;
  }


  .timestamp {
    height: 4rem;
    font-size: 1.25rem;

    &:last-child {
      height: auto;
      max-height: 8rem;
      min-height: 5.25rem;
    }
  }
}


.wishes {
  &-list {

  }

  &-list-item {
    display: flex;
    align-items: flex-start;
  }

  &-list-mark {
    font-size: 3.5rem;
    color: var(--red-500);
    padding-right: 1rem;
    text-align: center;
    min-width: 4rem;
    max-width: 4rem;
    line-height: 1.2;
  }
}

.countdown {
  text-transform: lowercase;
  ul {
    display: grid;
    grid-template-columns: repeat(4, minmax(0, 1fr));
    list-style: none;
    padding: 0;
    margin: 0;
  }
  li {
    display: flex;
    flex-direction: column;
    text-align: center;
    span {
      font-size: 3rem;
      color: var(--red-500);
    }
  }
}

.gallery {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  grid-column-gap: 1rem;
  grid-row-gap: 1rem;

  img {
    width: 100%;
    height: auto;
    min-height: 230px;
    max-height: 350px;
    object-fit: cover;
  }
}

.guest-list-columns {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(1, 1fr);
  grid-column-gap: .5rem;
}
