.ff-mak {
  font-family: "Mak", Georgia, serif;
}

.text-hero {
  font-size: 2.3rem;
  font-family: "Mak", Georgia, serif;
}

h1 {
  font-size: 3.5rem;
  line-height: 1.2;
  font-family: "Mak", Georgia, serif;
  font-weight: normal;
}

h3 {
  font-size: 1.5rem;
  text-transform: uppercase;
}

.hero-date {
  position: relative;
  padding-right: .5rem;
  &:after {
    content: '';
    width: 1px;
    height: 100%;
    background-color: var(--gray-500);
    display: inline-block;
    position: absolute;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;
  }
}

p {
  font-size: 1.25rem;
  color: var(--gray-500);
}

ul.list {
  padding: 0;
  margin: 0;

  & > li {
    list-style: none;
    padding: 0;
    margin: 0;
    color: var(--gray-500);
    font-size: 1.25rem;
    line-height: 1.4;
    text-align: center;
  }

  img {
    margin: 0 auto;
  }
}

a {
  color: var(--red-500);
}

.floating-button {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  width: 100%;
  background: var(--red-500);
  color: white;
  border: none;
  box-shadow: none;
  outline: none;
  height: 40px;
  border-radius: .5rem;
  margin: auto;
  text-decoration: none;
  text-transform: uppercase;
}

b {
  font-weight: bold;
}
