/* Webfont: Mak-Light */@font-face {
  font-family: 'MakLight';
  src: url('../fonts/mak/MAK.eot'); /* IE9 Compat Modes */
  src: url('../fonts/mak/MAK.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
  url('../fonts/mak/MAK.woff') format('woff'), /* Modern Browsers */
  url('../fonts/mak/MAK.ttf') format('truetype'); /* Safari, Android, iOS */
  font-style: normal;
  font-weight: normal;
  text-rendering: optimizeLegibility;
}

/* Webfont: Mak-Bold */@font-face {
  font-family: 'Mak';
  src: url('../fonts/mak/MAK-bold.eot'); /* IE9 Compat Modes */
  src: url('../fonts/mak/MAK-bold.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
  url('../fonts/mak/MAK-bold.woff') format('woff'), /* Modern Browsers */
  url('../fonts/mak/MAK-bold.ttf') format('truetype'); /* Safari, Android, iOS */
  font-style: normal;
  font-weight: bold;
  text-rendering: optimizeLegibility;
}

